.navbar {
    background-color: darkcyan;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid gray;
}

.navbar section {
    display: grid;
    grid-auto-flow: column;
}

@media screen and (max-width: 600px)
{
    .navbar section {
        font-size: 3.5vw;
    }
}

/* Silis Kleemoff */
.nav-logo .logo {
    color: white;
    margin-left: 25px;
    margin-right: 25px;
    transition: 0.3s;
}

.logo:hover {
    text-shadow: 0px 0.5px 3px whitesmoke;
    transition: 0.3s;
}

/* Games/Web Apps */
.nav-links li {
    color: aquamarine;
    margin-left: 10px;
    margin-right: 10px;
}

a {
    color: aquamarine;
}