@import url('https://fonts.googleapis.com/css2?family=Biryani:wght@200;700&display=swap');

* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    font-family: 'Biryani', sans-serif;
}

body {
    color: white;
    background-color: rgb(41, 41, 41);
}

p, li, a, h1, h2, h3, h4, h5, h6 {
    display: inline-block;
}

span {
    color:white;
}

.App {
    height: 100vh;
}

.image-circle {
    border: 1px solid gray;
    border-radius: 100%;
    margin-top: 25px;
    text-align: center;
    width: 256px;
    height: 256px;
    overflow: hidden;
    box-shadow: 1px 1px 5px gray;
}

.image-circle img {
    margin: 0;
}

img {
    width: 100%;
    margin-top: 25px;
    margin-bottom: 25px;
}

@media screen and (max-width: 600px)
{
    .image-circle {
        width: 75vw;
        height: 75vw;
    }
}

u {
    text-decoration: underline;
}