.section-type1 {
    margin-top: 15px;
    display: grid;
    grid-auto-flow: row;
    place-items: center;
    margin-left: 25vw;
    margin-right: 25vw;
    border-bottom: 2px solid gray;
}

@media screen and (max-width: 600px)
{
    .section-type1 {
        margin-left: 5vw;
        margin-right: 5vw;
    }
}

p {
    margin-top: 10px;
    margin-bottom: 10px;
}

.top-projects {
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
}

.project {
    padding: 10px;
    border: 1px solid gray;
    border-radius: 8px;
    display: grid;
    margin-top: 28px;
    /* margin-bottom: 10px; */
}

.project img {
    margin-top: 0;
    border-radius: 6px;
}